.categoryCard {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.photo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0.4;
}

.title {
  flex: 0.2;
}

.buttonSpace {
  display: flex;
  flex-direction: row;
  justify-content: right;
  flex: 0.3;
}
